.visually-hidden {
    position: absolute;
    white-space: nowrap;
    width: 1px;
    height: 1px;
    overflow: hidden;
    border: 0;
    padding: 0;
    clip: rect(0 0 0 0);
    clip-path: inset(50%);
    margin: -1px;
}
.contacts {
    padding-top: 129px;
    padding-bottom: 113px;
    margin: 0 auto;
    @include font(12px, 500, 1.5, null);
    background-color: var(--secondary-bg-color);
    background-image: linear-gradient(to bottom, var(--main-white) 12%,var(--secondary-bg-color) 12%);
    &__item-when {
        li:nth-child(2n) {
            margin-bottom: 10px;
        }
        @include font(12px, 500, 1.86, null);
        padding-bottom: 25px;
    }
    &__item {
    max-width: 100%;
    padding: 43px 20px;
    background-color: var(--main-white);
    box-shadow: 0px 8px 30px rgba(212, 20, 67, 0.1);
    border-radius: 24px;

        &--black {
            color: var(--main-black);
        }
        &--grey {
            line-height: 1.5;
            color: var(--main-text-color);
        }       
        &--pink {
        color: var(--secondary-pink-color);
        }
    }
    &__link {
        &--grey {
            color: var(--main-text-color);
        }
    }
    &__modal-item {
        display: flex;
        justify-content: center;
    }
    &__modal-item:first-child {
        margin-bottom: 10px;
    }
}
.contacts__item-where .contacts__item--black:first-child {
    margin-bottom: 10px;
}
.contacts__list {
    width: 100%;
    max-width: 380px;
    font-weight: 500;
    margin: 0 auto 54px;
}

.contacts__item-title {
    display: inline-flex;
    margin-bottom: 25px;
    padding: 0 25px;
    @include font(12px, 500, 1.86, 0.04em);
    align-items: center;
    text-align: center;
    text-transform: uppercase;
    color: var(--main-white);
    background: var(--coffee-color);
    border-radius: 4px;
    &.green {
        background-color: var(--green-color) ;
    }
}

// _________Полоса__________

.contacts__item-where::after,
.contacts__item-when::after {
    display: block;
    width: 100%;
    margin: 25px 0;
    content: '';
    border: 1px solid #E1E1E1;
}
.contacts__button {
    width: 162px;
    height: 44px;
    @include font(16px, 700, 1.31, null);
}
